.app{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
}

.app_container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 20rem;
  height: 30rem;
  background-color: white;
  background: rgba(255, 255, 255, 0.6);
  box-shadow: 0 0.5rem 2rem 0 rgba(132, 144, 255, 0.4);
  backdrop-filter: blur(0.3rem);
  -webkit-backdrop-filter: blur(0.3rem);
  border-radius: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
}


.app_container_img{
  width: 13rem;
  height: 13rem;
  background: linear-gradient(#fff, #fff) padding-box,
  linear-gradient(to right, #63bcfc, #a584ff) border-box;
  border: 0.3rem solid transparent;
  border-radius: 50%;
}

.app_container_text_box{
  padding-left: 2rem;
  padding-right: 2rem;
}

.app_container_name{
  font-weight: bolder;
  font-size: 1.5rem;
  text-align: left;
}


.app_container_text{
  font-size: 0.8rem;
  font-weight: bold;
  word-break: break;
}

.app_container_social{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 20%;
}

.app_container_social_icon{
  margin-top: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  opacity: 0.8;
  transform: delay(100ms);
  transition: all 500ms ease-in-out;
}

.app_container_social_icon:hover{
  transform: translateY(-0.75rem);
}


@media screen and (min-width: 576px) {
  .app_container{
    width: 35rem;
    height: 40rem;
  }

  .app_container_img{
    width: 15rem;
    height: 15rem;
  }

  .app_container_name{
    font-size: 2rem;
  }

  .app_container_text{
    font-size: 1rem;
  }

  .app_container_social_icon{
    width: 3rem;
    height: 3rem;
  }
}

@media screen and (min-width: 992px) {
  .app_container_name{
    font-size: 2rem;
  }

  .app_container_text{
    font-size: 1rem;
  }
}

@media screen and (min-width: 2000px){
  .app_container{
    width: 60rem;
    height: 60rem;
  }

  .app_container_img{
    width: 25rem;
    height: 25rem;
  }

  .app_container_name{
    font-size: 3rem;
  }

  .app_container_text{
    font-size: 1.5rem;
  }

  .app_container_social_icon{
    width: 5rem;
    height: 5rem;
  }
}